.container {
	// margin-top: 80px;
	opacity: 0;
	&.loaded {
		opacity: 1;
		animation: in 2s;
		transition: opacity 2s ease-in-out;
	}
}
.container-enter {
	opacity: 0.01;
}

.container-enter.container-enter-active {
	opacity: 1;
	transition: opacity 500ms ease-in;
}

.container-leave {
	opacity: 1;
}

.container-leave.container-leave-active {
	opacity: 0.01;
	transition: opacity 300ms ease-in;
}
@-webkit-keyframes in {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

@keyframes in {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}
